import axios from 'axios';
import React from 'react'
import { useState, useEffect } from 'react';
import { Toaster, toast } from 'react-hot-toast';
import { Navigate, useNavigate } from 'react-router-dom';


function Uploadmusic() {
  const Navigate = useNavigate();
  const [musictitle, setmusictitle] = useState();
  const [musicfile, setMusicFile] = useState();
  const [musicthumbnail, setMusicThumbnail] = useState();



  let channelid = localStorage.getItem("curntchannelid")
  let userid = localStorage.getItem("currentuserid");
  console.log("ssss", musicthumbnail, musicfile)

  const Uploadmusic = (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("channel_id", channelid)
    formData.append("user_id", userid)
    formData.append("music_title", musictitle)
    formData.append("music_file", musicfile)
    formData.append("music_thumbnail", musicthumbnail)
    formData.append("time_zone", "44411")



    axios.post("http://157.66.191.24:3003/api/upload_music", formData).then((res) => {

      toast.success(res.data.msg);

      setTimeout(() => {
        Navigate("/mpage")
      }, 3000)
    }).catch((error) => {
      toast.error("Failed to video uplode!");


    })
  }


  return (
    <div>
      <Toaster />
      <div className="single-channel-page" id="content-wrapper">
        <form onSubmit={Uploadmusic}>
          <div className="container-fluid upload-details">
            <div className="row">
              <div className="col-lg-12">
                <div className="main-title">
                  <h6>Upload Details</h6>
                </div>
              </div>
              <div className="col-lg-2">
                <div className="imgplace"></div>
              </div>

            </div>
            <div className="row mt-5">
              <div className="col-lg-12">
                <div className="osahan-form">

                  <div className="row">

                    <div className="col-lg-6">
                      <div className="form-group">
                        <label htmlFor="e1">Music Title</label>
                        <input required type="text" placeholder="Music Title" id="e1" className="form-control" onChange={((e) => setmusictitle(e.target.value))} />
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="form-group">
                        <label htmlFor="music-file">Music File</label>
                        <input required="file" type="file" accept="audio/*" placeholder="No File Chosen" id="music-file" className="form-control" onChange={(e) => setMusicFile(e.target.files[0])} />
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="form-group">
                        <label htmlFor="music-thumbnail">Music Thumbnail</label>
                        <input required="file" type="file" accept=".jpg, .png, image/*" placeholder="Music Thumbnail" id="music-thumbnail" className="form-control" onChange={(e) => setMusicThumbnail(e.target.files[0])} />
                      </div>
                    </div>
                  </div>
                </div>


              </div>
            </div>
            <hr />
            <div className="osahan-area text-center mt-3">
              <button type='submit' className="btn btn-outline-primary">Upload Music</button>
            </div>
          </div>
        </form>

      </div>
    </div>
  )
}

export default Uploadmusic
