import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import './slick-custom.css';
function Home() {
  const navigate = useNavigate();
  const [videodatas, setvideodatas] = useState();
  useEffect(() => {
    getlatestvideos();
  }, [0])
  const getlatestvideos = () => {

    axios.post("http://157.66.191.24:3003/api/get_latest_video").then((res) => {
      // console.log(res);
      setvideodatas(res.data.data)
    }).catch((error) => {

    })

  }

  const clicData = () => {
    navigate("/vpage")
  }

  const settings = {
    autoplay: false,
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 7,
    slidesToScroll: 1,
    nextArrow: <div className="slick-next" />,
    prevArrow: <div className="slick-prev" />,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        }
      }
    ]

  };

  const [videodata, setvideodata] = useState([]);
  const [searchdata, setSearchdata] = useState('');
  const [filteredVideos, setFilteredVideos] = useState([]);

  console.log(searchdata);
  useEffect(() => {
    getlatestvideo();
  }, [0])
  const getlatestvideo = () => {

    axios.post("http://157.66.191.24:3003/api/get_latest_video").then((res) => {
      // console.log(res);
      setvideodata(res.data.data)
    }).catch((error) => {

    })

  }
  useEffect(() => {
    const results = videodata.filter(video =>
      video.video_name.toLowerCase().includes(searchdata.toLowerCase())
    );
    setFilteredVideos(results);
  }, [searchdata, videodata]);
  return (
    <div>

      <div className="single-channel-page" id="content-wrapper">
        <div className="container-fluid pb-0">
          <div className="top-mobile-search">
            <div className="row">
              <div className="col-md-12">
                <form className="mobile-search">
                  <div className="input-group">
                    <input onChange={(e) => setSearchdata(e.target.value)} value={searchdata} type="text" placeholder="Search for..." className="form-control" />
                    <div className="input-group-append">
                      <button type="button" className="btn btn-dark"><i className="fas fa-search" /></button>
                    </div>
                  </div>
                  {searchdata && (
                    <div className="boxsearch ">
                      {filteredVideos.length > 0 ? (
                        filteredVideos.map((data, index) => (
                          <div key={index}>
                            <Link to='/vpage' className='ml-3' onClick={(() => {
                              setSearchdata("")
                              localStorage.setItem("videoid", data?._id)
                              localStorage.setItem("channelID", data?.channel_id)
                              localStorage.setItem("singleuserID", data?.user_id)
                            })}>{data?.video_name}</Link>

                          </div>
                        ))
                      ) : (
                        <p>No videos found.</p>
                      )}
                    </div>
                  )}
                </form>
              </div>
            </div>
          </div>
          <div className="top-category section-padding mb-4">
            <div className="row">
              <div className="col-md-12">
                <div className="main-title">
                  <div className="btn-group float-right right-action">
                    <a href="#" className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      <i className="fa fa-ellipsis-h" aria-hidden="true" />
                    </a>
                    <div className="dropdown-menu dropdown-menu-right">
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-star" /> &nbsp; Top Rated</a>
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a>
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                    </div>
                  </div>
                  <h6>Channels Categories</h6>
                </div>
              </div>
              <div className="col-md-12 ">
                <div className="" style={{ backgroundColor: "rgb(238 218 221)" }}>
                  <Slider {...settings}>
                    {videodatas?.map((data) => {
                      return (
                        <div class="item ">
                          <div class="category-item" style={{ display: "flex", justifyContent: "center", }}>
                            <Link to='/vpage' onClick={(() => {
                              localStorage.setItem("videoid", data?._id)
                              localStorage.setItem("channelID", data?.channel_id)
                              localStorage.setItem("singleuserID", data?.user_id)
                            })}>


                              {
                                data?.video[1]?.filename ? <img className="img-fluid" style={{ objectFit: 'cover' }} src={`http://157.66.191.24:3003/uploads/` + data?.video[1]?.filename} alt /> :
                                  <img class="img-fluid" src="img/s5.png" alt="" />}

                              <Link className='text-capitalize' to='/spage' >{data?.channel_name !== "null" ? data?.channel_name : "Youtub"}</Link>
                              <p>{data?.video_views} views</p>
                            </Link>
                          </div>
                        </div>



                      )
                    })

                    }
                  </Slider>

                </div>
              </div>
            </div>
          </div>

          <hr />
          <div className="video-block section-padding">
            <div className="row">
              <div className="col-md-12">
                <div className="main-title">
                  <div className="btn-group float-right right-action">
                    <a href="#" className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      Sort by <i className="fa fa-caret-down" aria-hidden="true" />
                    </a>
                    <div className="dropdown-menu dropdown-menu-right">
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-star" /> &nbsp; Top Rated</a>
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a>
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                    </div>
                  </div>
                  <h6>Featured Videos</h6>
                </div>
              </div>

              {videodatas?.map((data) => {
                return (<div className="col-xl-3 col-sm-6 mb-3">
                  <div className="video-card">

                    <div className="video-card-image">
                      <Link to='/vpage' onClick={(() => {
                        localStorage.setItem("videoid", data?._id)
                        localStorage.setItem("channelID", data?.channel_id)
                      })} className="play-icon" href="#"><i className="fas fa-play-circle" /></Link>
                      <Link to="/vpage" onClick={(() => {
                        localStorage.setItem("videoid", data?._id)
                        localStorage.setItem("channelID", data?.channel_id)
                      })}>{
                          data?.video[1]?.filename ? <img className="img-fluid" style={{ height: '158px' }} src={`http://157.66.191.24:3003/uploads/` + data?.video[1]?.filename} alt /> :
                            <img className="img-fluid" src="img/v1.png" alt />}
                      </Link>
                      {/* <div className="time">3:50</div> */}
                    </div>
                    <div className="video-card-body">
                      <div className="btn-group float-right right-action">
                        <a href="#" className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                          <i style={{ color: "#dc3545" }} className="fas fa-fw fa-ellipsis-v" aria-hidden="true" />
                        </a>
                        <div className="dropdown-menu dropdown-menu-right">
                          <Link className="dropdown-item" type='button'><i className="fas fa-fw fa-trash" /> &nbsp; Delete Video</Link>
                          {/* <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a> */}
                          <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                        </div>


                      </div>
                      <div className="video-title">
                        <Link to="/vpage" onClick={(() => {
                          localStorage.setItem("videoid", data?._id)
                          localStorage.setItem("channelID", data?.channel_id)

                        })} className='text-capitalize'>{data?.video_name}</Link>
                      </div>
                      <div className="video-page text-success">
                        <Link to='/spage'>  {data?.channel_name}</Link><Link title data-placement="top" data-toggle="tooltip" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></Link>
                      </div>
                      <div className="video-view">
                        {data?.video_views} &nbsp;<i className="fas fa-calendar-alt" /> {data?.current_date}
                      </div>
                    </div>
                  </div>
                </div>)
              })

              }


            </div>
          </div>
          <hr className="mt-0" />
          <div className="video-block section-padding">
            <div className="row">
              <div className="col-md-12">
                <div className="main-title">
                  <div className="btn-group float-right right-action">
                    <a href="#" className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      Sort by <i className="fa fa-caret-down" aria-hidden="true" />
                    </a>
                    <div className="dropdown-menu dropdown-menu-right">
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-star" /> &nbsp; Top Rated</a>
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a>
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                    </div>
                  </div>
                  <h6>Popular Channels</h6>
                </div>
              </div>
              <div className="col-xl-3 col-sm-6 mb-3">
                <div className="channels-card">
                  <div className="channels-card-image">
                    <a href="#"><img className="img-fluid" src="img/s1.png" alt /></a>
                    <div className="channels-card-image-btn"><button type="button" className="btn btn-outline-danger btn-sm">Subscribe <strong>1.4M</strong></button></div>
                  </div>
                  <div className="channels-card-body">
                    <div className="channels-title">
                      <a href="#">Channels Name</a>
                    </div>
                    <div className="channels-view">
                      382,323 subscribers
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-xl-3 col-sm-6 mb-3">
                <div className="channels-card">
                  <div className="channels-card-image">
                    <a href="#"><img className="img-fluid" src="img/s2.png" alt /></a>
                    <div className="channels-card-image-btn"><button type="button" className="btn btn-outline-danger btn-sm">Subscribe <strong>1.4M</strong></button></div>
                  </div>
                  <div className="channels-card-body">
                    <div className="channels-title">
                      <a href="#">Channels Name</a>
                    </div>
                    <div className="channels-view">
                      382,323 subscribers
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-xl-3 col-sm-6 mb-3">
                <div className="channels-card">
                  <div className="channels-card-image">
                    <a href="#"><img className="img-fluid" src="img/s3.png" alt /></a>
                    <div className="channels-card-image-btn"><button type="button" className="btn btn-outline-secondary btn-sm">Subscribed <strong>1.4M</strong></button></div>
                  </div>
                  <div className="channels-card-body">
                    <div className="channels-title">
                      <a href="#">Channels Name <span title data-placement="top" data-toggle="tooltip" data-original-title="Verified"><i className="fas fa-check-circle" /></span></a>
                    </div>
                    <div className="channels-view">
                      382,323 subscribers
                    </div>
                  </div>
                </div>
              </div>
              <div className="col-xl-3 col-sm-6 mb-3">
                <div className="channels-card">
                  <div className="channels-card-image">
                    <a href="#"><img className="img-fluid" src="img/s4.png" alt /></a>
                    <div className="channels-card-image-btn"><button type="button" className="btn btn-outline-danger btn-sm">Subscribe <strong>1.4M</strong></button></div>
                  </div>
                  <div className="channels-card-body">
                    <div className="channels-title">
                      <a href="#">Channels Name</a>
                    </div>
                    <div className="channels-view">
                      382,323 subscribers
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
  )
}

export default Home
