import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { fetchProfile } from '../Redux/Slice/Todo';

function Help() {
  const dispatch = useDispatch();
  const { data: profileData } = useSelector((state) => state.profile);
  useEffect(() => {
    dispatch(fetchProfile)
  }, [dispatch])

  useEffect(() => {
    const postData = { _id: "65e01385abd7898d671f6e86" };

    dispatch(fetchProfile(postData));

  }, [dispatch])


  return (
    <div>

      <div className="single-channel-page" id="content-wrapper">
        <div className="container-fluid">
          <div className="video-block section-padding">
            <div className="row">
              <div className="col-md-12">
                <div className="main-title">

                  <h6>Downlodes video</h6>
                </div>
              </div>

              <img height={200} width={200}
                src={`http://157.66.191.24:3003/uploads/${profileData?.data?.profile_image?.filename || ""
                  }`}
                alt="Profile from Profile Data"
              />



            </div>
            <nav aria-label="Page navigation example">
              <ul className="pagination justify-content-center pagination-sm mb-0">
                <li className="page-item disabled">
                  <a className="page-link" href="#" tabIndex={-1}>Previous</a>
                </li>
                <li className="page-item active"><a className="page-link" href="#">1</a></li>
                <li className="page-item"><a className="page-link" href="#">2</a></li>
                <li className="page-item"><a className="page-link" href="#">3</a></li>
                <li className="page-item">
                  <a className="page-link" href="#">Next</a>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Help
