import axios from 'axios';
import React, { useEffect, useState } from 'react'
import ReactAudioPlayer from 'react-audio-player';
import { Link } from 'react-router-dom';



function Music() {

  const [MusicData, setMusicData] = useState();
  let musicId = localStorage.getItem("musicid")

  useEffect(() => {
    getprofile();
  }, [0])
  const getprofile = () => {
    const data = {
      music_id: musicId
    }
    axios.get("http://157.66.191.24:3003/api/get_music", data).then((res) => {
      console.log(res);
      setMusicData(res.data.data)
    }).catch((error) => {
      console.log("error", error)
    })
  }
  return (
    <div>
      <div className="single-channel-page" id="content-wrapper">
        <div className="container-fluid">
          <div className="video-block section-padding">
            <div className="row">
              <div className="col-md-12">
                <div className="main-title">
                  <div className="btn-group float-right right-action">
                    <a href="#" className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                      Sort by <i className="fa fa-caret-down" aria-hidden="true" />
                    </a>
                    <div className="dropdown-menu dropdown-menu-right">
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-star" /> &nbsp; Top Rated</a>
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a>
                      <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                    </div>
                  </div>
                  <h6>Watch History</h6>
                </div>
              </div>

              {MusicData?.map((data) => {
                return (

                  <div className="col-xl-3 col-sm-6 mb-3">
                    <div className="video-card history-video">
                      <div className="video-card-image">
                        <a className="video-close" href="#"><i className="fas fa-times-circle" /></a>
                        <Link to='/play' onClick={(() => { localStorage.setItem("musicid", data?._id) })} className="play-icon" href="#"><i className="fas fa-play-circle" /></Link>
                        <Link onClick={(() => { localStorage.setItem("musicid", data?._id) })}  >
                          {data?.music[1]?.filename ? <img style={{ height: "150px" }} className="img-fluid" src={`http://157.66.191.24:3003/uploads/` + data?.music[1]?.filename} alt /> :
                            <img className="img-fluid" src="img/v2.png" alt />}
                        </Link>
                        <div className="time">3:50</div>
                      </div>
                      <div className="progress">
                        <div className="progress-bar" role="progressbar" style={{ width: '50%' }} aria-valuenow={50} aria-valuemin={0} aria-valuemax={100}>1:40</div>
                      </div>
                      <div className="video-card-body">
                        <div className="video-title">
                          <Link onClick={(() => { localStorage.setItem("musicid", data?._id) })}  >{data?.music_title}</Link>
                        </div>
                        <div className="video-page text-success">
                          {data?.data[0]?.channel_name}  <Link onClick={(() => { localStorage.setItem("musicid", data?._id) })} title data-placement="top" data-toggle="tooltip" href="#" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></Link>
                        </div>
                        <div className="video-view">
                          1.8M views &nbsp;<i className="fas fa-calendar-alt" /> 11 Months ago
                        </div>
                      </div>
                    </div>
                  </div>
                )
              })}

            </div>
            <nav aria-label="Page navigation example">
              <ul className="pagination justify-content-center pagination-sm mb-0">
                <li className="page-item disabled">
                  <a className="page-link" href="#" tabIndex={-1}>Previous</a>
                </li>
                <li className="page-item active"><a className="page-link" href="#">1</a></li>
                <li className="page-item"><a className="page-link" href="#">2</a></li>
                <li className="page-item"><a className="page-link" href="#">3</a></li>
                <li className="page-item">
                  <a className="page-link" href="#">Next</a>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Music
