import React, { useState } from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import axios from 'axios';
import { useNavigate, navigate } from 'react-router-dom';
import { Toaster, toast } from 'react-hot-toast'

function Register() {
  const navigate = useNavigate();
  const [Username, setUsername] = useState();
  const [profilepic, setprofilepic] = useState();
  const [password, setPassword] = useState();
  const [gender, setGender] = useState();
  const [cpassword, setCpassword] = useState();
  const [Email, setEmail] = useState();



  const handledata = (e) => {
    e.preventDefault();
    if (password !== cpassword) {
      toast.error("Passwords do not match")

      return;
    }

    const data = {
      username: Username,
      email: Email,
      password: password,
      confirm_password: cpassword,
      gender: gender,
      fcm: "1234"

    }
    // const formData = new FormData();
    // formData.append("id", "user")
    // formData.append("profile", profilepic)
    // formData.append("mobile", mobilenumber)
    // formData.append("Password", password)
    // formData.append("Gender", gender)
    axios.post("http://157.66.191.24:3003/api/signup", data).then((res) => {


      localStorage.setItem("currentuserid", res.data.data[0]._id)
      localStorage.setItem("username", res.data.data[0].username)
      // console.log(res.data.data[0]._id, "jijiiji");


      if (res.data.result === "false") {
        toast.error(res.data.msg)
      } else {
        toast.success(res.data.msg)
        setTimeout(() => {
          navigate("/home")

        }, 3000)
      }
    }).catch((error) => {

      toast.error("some issues has been occured please try again with diffrent emailID")
    })
  }

  const settings = {
    autoplay: true,
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };

  return (
    <div>
      <Toaster />
      <section className="login-main-wrapper single-channel-page">

        <div className="container-fluid pl-0 pr-0">
          <div className="row no-gutters">
            <div className="col-md-5 p-5 bg-white full-height">
              <div className="login-main-left">
                <div className="text-center mb-5 login-main-left-header pt-4">
                  <img src="img/favicon.png" className="img-fluid" alt="LOGO" />
                  <h5 className="mt-3 mb-3">Welcome to Vidoe</h5>
                  <p>It is a long established fact that a reader <br /> will be distracted by the readable.</p>
                </div>

                <form onSubmit={handledata}>
                  <div className="form-group">
                    <label>User Name</label>
                    <input onChange={((e) => { setUsername(e.target.value) })} type="text" required className="form-control" placeholder="User Name" />
                  </div>
                  <div className="form-group">
                    <label>Email</label>
                    <input type="email" className="form-control" placeholder="Email" onChange={((e) => { setEmail(e.target.value) })} />
                  </div>
                  <div className="form-group">
                    <label>Password</label>
                    <input type="password" className="form-control" placeholder="Password" onChange={((e) => { setPassword(e.target.value) })} />
                  </div>
                  <div className="form-group">
                    <label>Confirm Password</label>
                    <input type="password" className="form-control" placeholder="Confirm Password" onChange={((e) => { setCpassword(e.target.value) })} />

                  </div>

                  {/* <div className="form-group">
                    <label>Profile-Pic</label>
                    <input type="file" onChange={((e) => { setprofilepic(e.target.files[0]) })} className="form-control" placeholder="Password" />
                  </div> */}
                  <div className="form-group">
                    <label>Gender</label>
                    <br />
                    <label className="radio-inline ml-5 ">
                      <input
                        type="radio"
                        name="gender"
                        value="male"
                        onChange={(e) => { setGender(e.target.value) }}
                      /> Male
                    </label>
                    <label className="radio-inline ml-5">
                      <input
                        type="radio"
                        name="gender"
                        value="female"
                        onChange={(e) => { setGender(e.target.value) }}
                      /> Female
                    </label>

                  </div>

                  <div className="mt-4">
                    <button type="submit" className="btn btn-outline-primary btn-block btn-lg">Sign Up</button>
                  </div>
                </form>
                <div className="text-center mt-5">
                  <p className="light-gray">Already have an Account? <a href="login.html">Sign In</a></p>
                </div>
              </div>
            </div>
            <div className="col-md-7 ">
              <Slider {...settings}>
                <div className="login-main-right bg-white p-5 mt-5 mb-5 ml-5">
                  <div className="carousel-login-card text-center">
                    <img src="img/login.png" className="img-fluid" alt="LOGO" />
                    <h5 className="mt-5 mb-3">Watch videos offline</h5>
                    <p className="mb-4">When an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries.</p>
                  </div>
                </div>
                <div className="login-main-right bg-white p-5 mt-5 mb-5  ml-5">
                  <div className="carousel-login-card text-center">
                    <img src="img/login.png" className="img-fluid" alt="LOGO" />
                    <h5 className="mt-5 mb-3">Download videos effortlessly</h5>
                    <p className="mb-4">When an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries.</p>
                  </div>
                </div>
                <div className="login-main-right bg-white p-5 mt-5 mb-5  ml-5">
                  <div className="carousel-login-card text-center">
                    <img src="img/login.png" className="img-fluid" alt="LOGO" />
                    <h5 className="mt-5 mb-3">Create GIFs</h5>
                    <p className="mb-4">When an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries.</p>
                  </div>
                </div>
              </Slider>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Register;
