import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios';
import "../components/Searchresultlist.css"



function Navbar() {
  const [username, setUsername] = useState('');
  const [profile, setprofile] = useState();
  const [imgdata, setimgData] = useState();
  let id = localStorage.getItem("currentuserid");
  let name = localStorage.getItem("username");


  // http://157.66.191.24:3003/api/get_my_channel
  useEffect(() => {
    getprofilee();
  }, [0])
  const getprofilee = () => {
    const data = {
      user_id: id
    }
    axios.post("http://157.66.191.24:3003/api/get_my_channel", data).then((res) => {
      // console.log("ijji", res);
      localStorage.setItem("curntchannelid", res.data.data[0]._id)
      localStorage.setItem("curntchannelname", res.data.data[0].channel_name)

    }).catch((error) => {

    })

  }


  useEffect(() => {
    getprofile();
  }, [0])
  const getprofile = () => {
    const data = {
      _id: id
    }

    axios.post("http://157.66.191.24:3003/api/view_profile", data).then((res) => {
      // console.log(res);

      setimgData(res.data.data)



    }).catch((error) => {


    })

  }
  const [videodata, setvideodata] = useState([]);
  const [searchdata, setSearchdata] = useState('');
  const [filteredVideos, setFilteredVideos] = useState([]);

  console.log(searchdata);
  useEffect(() => {
    getlatestvideo();
  }, [0])
  const getlatestvideo = () => {

    axios.post("http://157.66.191.24:3003/api/get_latest_video").then((res) => {
      // console.log(res);
      setvideodata(res.data.data)
    }).catch((error) => {

    })

  }
  useEffect(() => {
    const results = videodata.filter(video =>
      video.video_name.toLowerCase().includes(searchdata.toLowerCase())
    );
    setFilteredVideos(results);
  }, [searchdata, videodata]);
  return (
    <>
      <nav className="navbar navbar-expand navbar-light bg-white static-top osahan-nav sticky-top">
        &nbsp;&nbsp;
        <button className="btn btn-link btn-sm text-secondary order-1 order-sm-0" id="sidebarToggle">
          <i className="fas fa-bars" />
        </button> &nbsp;&nbsp;
        <Link to='/home' className="navbar-brand mr-1" href="index.html"><img className="img-fluid" alt src="img/logo.png" /></Link>
        {/* Navbar Search */}
        <form className="d-none d-md-inline-block form-inline ml-auto mr-0 mr-md-5 my-2 my-md-0 osahan-navbar-search">
          <div className="input-group">
            <input onChange={(e) => setSearchdata(e.target.value)} value={searchdata} type="text" className="form-control" placeholder="Search for..." />
            <div className="input-group-append">
              <button className="btn btn-light" type="button">
                <i className="fas fa-search" />
              </button>
            </div>
          </div>

          {searchdata && (
            <div className="boxsearch ">
              {filteredVideos.length > 0 ? (
                filteredVideos.map((data, index) => (
                  <div key={index}>
                    <Link to='/vpage' className='ml-3' onClick={(() => {
                      setSearchdata("")
                      localStorage.setItem("videoid", data?._id)
                      localStorage.setItem("channelID", data?.channel_id)
                      localStorage.setItem("singleuserID", data?.user_id)
                    })}>{data?.video_name}</Link>

                  </div>
                ))
              ) : (
                <p>No videos found.</p>
              )}
            </div>
          )}

        </form>



        {/* Navbar */}
        <ul className="navbar-nav ml-auto ml-md-0 osahan-right-navbar">
          <li className="nav-item dropdown no-arrow mx-1">
            <a className="nav-link dropdown-toggle" href="#" id="alertsDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              <i className="fas fa-plus-circle fa-fw" />
              Upload
            </a>
            <div className="dropdown-menu dropdown-menu-right" aria-labelledby="alertsDropdown">
              <Link to='/upload' className="dropdown-item" href="#"> <i className="fas fa-fw fa-cloud-upload-alt" /> &nbsp; Upload video</Link>
              <Link to='/uploadm' className="dropdown-item" href="#"><i className="fas fa-fw fa-music" /> &nbsp; Upload Music</Link>

            </div>
          </li>

          <li className="nav-item dropdown no-arrow mx-1">
            <a className="nav-link dropdown-toggle" href="#" id="alertsDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              <i className="fas fa-bell fa-fw" />
              <span className="badge badge-danger">9+</span>
            </a>
            <div className="dropdown-menu dropdown-menu-right" aria-labelledby="alertsDropdown">
              <a className="dropdown-item" href="#"><i className="fas fa-fw fa-edit " /> &nbsp; Action</a>
              <a className="dropdown-item" href="#"><i className="fas fa-fw fa-headphones-alt " /> &nbsp; Another action</a>
              <div className="dropdown-divider" />
              <a className="dropdown-item" href="#"><i className="fas fa-fw fa-star " /> &nbsp; Something else here</a>
            </div>
          </li>
          <li className="nav-item dropdown no-arrow mx-1">
            <a className="nav-link dropdown-toggle" href="#" id="messagesDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              <i className="fas fa-envelope fa-fw" />
              <span className="badge badge-success">7</span>
            </a>
            <div className="dropdown-menu dropdown-menu-right" aria-labelledby="messagesDropdown">
              <a className="dropdown-item" href="#"><i className="fas fa-fw fa-edit " /> &nbsp; Action</a>
              <a className="dropdown-item" href="#"><i className="fas fa-fw fa-headphones-alt " /> &nbsp; Another action</a>
              <div className="dropdown-divider" />
              <a className="dropdown-item" href="#"><i className="fas fa-fw fa-star " /> &nbsp; Something else here</a>
            </div>
          </li>
          <li className="nav-item dropdown no-arrow osahan-right-navbar-user">
            <a className="nav-link dropdown-toggle user-dropdown-link" href="#" id="userDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
              {/* <img alt="Avatar" src="img/user.png" /> */}
              {
                imgdata?.profile_image?.filename ? (<img alt="Avatar" src={`http://157.66.191.24:3003/uploads/` + imgdata?.profile_image?.filename} />) : (<img alt="Avatar"
                  src="img/user.png" />)
              }

              {name}
            </a>
            <div className="dropdown-menu dropdown-menu-right" aria-labelledby="userDropdown">
              <Link to='/account' className="dropdown-item"><i className="fas fa-fw fa-user-circle" /> &nbsp; My Account</Link>
              <Link to='/profile' className="dropdown-item"><i className="fas fa-fw fa-user-plus" /> &nbsp;   Create Profile</Link>

              <Link to='/create' className="dropdown-item"><i className="fas fa-fw fa-plus-circle" /> &nbsp; Create Channel  </Link>
              <Link to='/sub' className="dropdown-item" ><i className="fas fa-fw fa-video" /> &nbsp; Subscriptions</Link>
              <Link to='/set' className="dropdown-item" ><i className="fas fa-fw fa-cog" /> &nbsp; Settings</Link>
              <div className="dropdown-divider" />
              {id !== null ? (<Link to='/log' className="dropdown-item" href="#" data-toggle="modal" data-target="#logoutModal"><i className="fas fa-fw fa-sign-out-alt" /> &nbsp; Logout</Link>) : (<Link to='/log' className="dropdown-item" href="#" data-toggle="modal" data-target="#logoutModal"><i className="fas fa-fw fa-sign-out-alt" /> &nbsp; Login</Link>)}
              {/* <Link to='/log' className="dropdown-item" href="#" data-toggle="modal" data-target="#logoutModal"><i className="fas fa-fw fa-sign-out-alt" /> &nbsp; Logout</Link> */}
            </div>
          </li>
        </ul>
      </nav >

    </>
  )
}

export default Navbar;