import React, { useEffect, useState } from 'react'
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { Toaster, toast } from 'react-hot-toast';



function CreateProfile() {
  const navigate = useNavigate();

  const [profilepic, setprofilepic] = useState();
  const [imgData, setimgData] = useState();
  let userid = localStorage.getItem("currentuserid")

  console.log(imgData);



  const createProfile = (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("_id", userid)
    formData.append("profile_image", profilepic)


    axios.post("http://157.66.191.24:3003/api/upload_user_image", formData).then((res) => {
      getprofile();
      toast.success("Profile created successfully!");
      setTimeout(() => {
        navigate("/home");
      }, 3000);
    }).catch((error) => {
      toast.error("Failed to create profile!");
    })
  }
  useEffect(() => {
    getprofile();
  }, [0])
  const getprofile = () => {
    const data = {
      _id: userid
    }
    axios.post("http://157.66.191.24:3003/api/view_profile", data).then((res) => {
      // console.log(res);
      setimgData(res.data.data)
    }).catch((error) => {

    })

  }

  return (

    <div>
      <Toaster />
      <div className="single-channel-page" id="content-wrapper">
        <div className="container-fluid upload-details">
          <div className="d-flex mt-3 mb-3" style={{ justifyContent: "center" }}>
            {
              imgData?.profile_image?.filename ? (<img class="img-thumbnail" style={{
                borderRadius: "20px", height: '200px', width: '200px'
              }} alt="" src={`http://157.66.191.24:3003/uploads/` + imgData?.profile_image?.filename} />) : (<img class="img-thumbnail" style={{
                borderRadius: "20px"
              }} alt="" src="img/s2.png" />)
            }




          </div>
          <form onSubmit={createProfile}>
            <div className="row">
              <div className="col-lg-12">
                <div className="osahan-form">
                  <div className="row" style={{ display: "flex", justifyContent: "center" }}>
                    <div className="col-lg-6">
                      <div className="form-group" >
                        <label htmlFor="e1">Create Image</label>
                        <input type="file" placeholder="User Image" id="e1" className="form-control" onChange={((e) => setprofilepic(e.target.files[0]))} />
                      </div>
                    </div>

                  </div>
                </div>
                <div className="osahan-area text-center mt-3">
                  <button type='submit' className="btn btn-outline-primary">Create profile</button>
                </div>
                <hr />

              </div>
            </div>
          </form>
        </div>


      </div>
    </div>
  )
}

export default CreateProfile
