import axios from 'axios';
import React, { useEffect, useState } from 'react'
import ReactPlayer from 'react-player'
import { Link } from 'react-router-dom';
import toast, { Toaster } from 'react-hot-toast';



function Videopage() {
  const [videoData, setvideoData] = useState();
  const [VideoData, setVideoData] = useState()
  let videoidd = localStorage.getItem("videoid")
  let channelID = localStorage.getItem("channelID")
  useEffect(() => {
    getprofile();
  },)
  const getprofile = (item) => {
    const data = {
      video_id: videoidd ? videoidd : item
    }
    // console.log("sss", data)
    axios.post("http://157.66.191.24:3003/api/get_single_video", data).then((res) => {
      //console.log("eeee", res)
      setvideoData(res.data.data)

    }).catch((error) => {
      // console.log("error", error)
    })


  }



  useEffect(() => {
    getprofiley();
  }, [0])
  const getprofiley = () => {

    axios.post("http://157.66.191.24:3003/api/get_latest_video").then((res) => {
      // console.log(res);
      setVideoData(res.data.data)
    }).catch((error) => {

    })

  }
  // let channelid = localStorage.getItem("curntchannelid")
  let userid = localStorage.getItem("currentuserid");
  useEffect(() => {
    gethistory();
  }, [0])
  const gethistory = (item) => {

    // const formData = new FormData();
    // formData.append("user_id", userid)
    // formData.append("channel_id", channelid)
    // formData.append("video_id", videoidd)

    const data = {
      user_id: userid,
      channel_id: channelID,
      video_id: videoidd ? videoidd : item,

    }
    axios.post("http://157.66.191.24:3003/api/create_history", data)
      .then((res) => {



      })
      .catch((error) => {

      });
  }
  useEffect(() => {
    Addviews();
  }, [0])
  const Addviews = (item) => {
    const data = {
      video_id: videoidd ? videoidd : item
    }

    axios.post("http://157.66.191.24:3003/api/video_views", data).then((res) => {



    }).catch((error) => {
      // console.log("error", error)
    })


  }
  const [downlodevideodata, setdownlodevideodata] = useState();
  let vdataid = localStorage.getItem("videoid")

  const downlodevideo = (item) => {
    const data = {
      user_id: userid,
      channel_id: channelID,
      video_id: videoidd ? videoidd : item

    }
    // console.log("dataa", data)
    axios.post("http://157.66.191.24:3003/api/download_video", data).then((res) => {
      setdownlodevideodata()
      // console.log("res", res)
      toast.success(res.data.msg)
    }).catch((error) => {
      // console.log("error", error)
    })

  }
  const [savewlaterdata, setsavewlater] = useState()
  const savewlater = (item) => {
    const data = {
      user_id: userid,
      channel_id: channelID,
      video_id: videoidd ? videoidd : item

    }
    // console.log("dataa", data)
    axios.post("http://157.66.191.24:3003/api/save_watch_later", data).then((res) => {
      setsavewlater()
      // console.log("lllll", res)
      toast.success(res.data.msg)
    }).catch((error) => {
      // console.log("mmmmm", error)
    })

  }


  const subscribec = () => {
    const data = {
      user_id: userid,
      channel_id: channelID,


    }
    // console.log("dataa", data)
    axios.post("http://157.66.191.24:3003/api/subscribe_to_channel", data).then((res) => {
      getsubscribec();
      toast.success(res.data.msg)

    }).catch((error) => {

    })

  }
  const [getsubscribe, setgetsubscribe] = useState()
  useEffect(() => {
    getsubscribec();
  }, [0])
  const getsubscribec = () => {
    const data = {
      user_id: userid,
      channel_id: channelID,


    }
    // console.log("dataa", data)
    axios.post("http://157.66.191.24:3003/api/get_subscribe_single_channel", data).then((res) => {
      setgetsubscribe(res.data.data)
      //console.log("ssssslllll", res)

    }).catch((error) => {
      //console.log("sssssmmmmm", error)
    })

  }
  return (
    <div>
      <Toaster />
      <div className="single-channel-page" id="content-wrapper">
        <div className="container-fluid pb-0">
          <div className="video-block section-padding">
            <div className="row">
              <div className="col-md-8">
                <div className="single-video-left">
                  <div className="single-video">
                    <ReactPlayer width='100%'
                      height='400px' controls='true' url={`http://157.66.191.24:3003/uploads/` + videoData?.video[0]?.filename} autoplay='true' playing='true' />
                    {/* <iframe width="100%" height={315} src="https://www.youtube-nocookie.com/embed/8LWZSGNjuF0?rel=0&controls=0&showinfo=0" frameBorder={0} allow="autoplay; encrypted-media" allowFullScreen /> */}
                  </div>
                  <div className="single-video-title box mb-3">
                    <div className="btn-group float-right right-action">
                      <a onClick={(() => { setdownlodevideodata() })} className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        <i style={{ color: "#dc3545" }} className="fas fa-fw fa-ellipsis-v" aria-hidden="true" />
                      </a>
                      <div className="dropdown-menu dropdown-menu-right">
                        <Link onClick={downlodevideo} className="dropdown-item" type='button'><i className="fas fa-download" /> &nbsp;  download Video</Link>
                        {/* <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a> */}
                        <Link onClick={savewlater} className="dropdown-item" href="#"><i className="fas fa-clock" /> &nbsp; Save watch later</Link>
                        <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>

                      </div>


                    </div>
                    <h2><a href="#" className='text-capitalize'>{videoData?.video_name}</a></h2>
                    <p className="mb-0"><i className="fas fa-eye" />{videoData?.video_views} views</p>
                  </div>
                  <div className="single-video-author box mb-3">
                    <div className="float-right">
                      {getsubscribe?.subscribe_status == "1" ?
                        <button className="btn btn-danger" type="button" onClick={subscribec}> Unsubscribe <strong>{videoData?.channel_data[0]?.subscribe_count}</strong></button> : <button className="btn btn-danger" type="button" onClick={subscribec}> Subscribe <strong>{videoData?.channel_data[0]?.subscribe_count}</strong></button>} <button className="btn btn btn-outline-danger" type="button"><i className="fas fa-bell" /></button></div>
                    {videoData?.channel_data[0]?.image[1]?.filename ?
                      <img className="img-fluid" src={`http://157.66.191.24:3003/uploads/` + videoData?.channel_data[0]?.image[1]?.filename} /> : <img className="img-fluid" src='img/s4.png' />}

                    <p><Link to='/spage'><strong className='text-capitalize'> {videoData?.channel_name} </strong></Link> <span title data-placement="top" data-toggle="tooltip" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></span></p>
                    <small>Published on {videoData?.current_date}</small>
                  </div>
                  <div className="single-video-info-content box mb-3">
                    <h6>Cast:</h6>
                    <p>Nathan Drake , Victor Sullivan , Sam Drake , Elena Fisher</p>
                    <h6>Category :</h6>
                    <p>{videoData?.category_type} , 1080p</p>
                    <h6>About :</h6>
                    <p className='text-capitalize'>{videoData?.description} </p>
                    <h6>Tags :</h6>
                    <p className="tags mb-0">
                      <span><a href="#">Uncharted 4</a></span>
                      <span><a href="#">Playstation 4</a></span>
                      <span><a href="#">Gameplay</a></span>
                      <span><a href="#">1080P</a></span>
                      <span><a href="#">ps4Share</a></span>
                      <span><a href="#">+ 6</a></span>
                    </p>
                  </div>
                </div>
              </div>
              <div className="col-md-4">
                <div className="single-video-right">
                  <div className="row">
                    <div className="col-md-12">

                      <div className="main-title">
                        <div className="btn-group float-right right-action">
                          <a href="#" className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            Sort by <i className="fa fa-caret-down" aria-hidden="true" />
                          </a>
                          <div className="dropdown-menu dropdown-menu-right">
                            <a className="dropdown-item" href="#"><i className="fas fa-fw fa-star" /> &nbsp; Top Rated</a>
                            <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a>
                            <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                          </div>
                        </div>
                        <h6>Up Next</h6>
                      </div>
                    </div>
                    <div className="col-md-12" style={{ overflow: 'scroll', height: '600px' }} >
                      {VideoData?.map((data) => {
                        return (

                          <div className="video-card video-card-list">
                            <div className="video-card-image">
                              <Link onClick={(() => {
                                localStorage.setItem("videoid", data?._id)
                                getprofile(data?._id)
                              })} className="play-icon" href="#"><i className="fas fa-play-circle" /></Link>
                              <Link onClick={(() => { localStorage.setItem("videoid", data?._id) })}>{
                                data?.video[1]?.filename ? <img className="img-fluid" style={{ height: '158px' }} src={`http://157.66.191.24:3003/uploads/` + data?.video[1]?.filename} alt /> :
                                  <img className="img-fluid" src="img/v1.png" alt />}
                              </Link>
                              {/* <div className="time">3:50</div> */}
                            </div>
                            <div className="video-card-body text-capitalize">

                              <div className="btn-group float-right right-action">
                                <a href="#" className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                  <i className="fa fa-ellipsis-v" aria-hidden="true" />
                                </a>
                                <div className="dropdown-menu dropdown-menu-right">
                                  <a className="dropdown-item" href="#"><i className="fas fa-fw fa-star" /> &nbsp; Top Rated</a>
                                  <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a>
                                  <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                                </div>
                              </div>
                              <div className="video-title">
                                <Link >{data?.video_name}</Link>
                              </div>
                              <div className="video-page text-success">
                                <Link to='/spage'>{data?.channel_name} </Link> <Link title data-placement="top" data-toggle="tooltip" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></Link>
                              </div>
                              <div className="video-view">
                                {data?.video_views} &nbsp;<i className="fas fa-calendar-alt" />  {data?.current_date}
                              </div>
                            </div>



                          </div>)
                      })

                      }
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>

    </div>
  )
}

export default Videopage