import axios from 'axios';
import React, { useEffect, useState } from 'react'
import ReactAudioPlayer from 'react-audio-player';
import { Link } from 'react-router-dom';


function Musicplay() {
  const [musicData, setvideoData] = useState();
  const [MusicData, setMusicData] = useState();
  // console.log("sss", videoData)
  let musicId = localStorage.getItem("musicid")

  useEffect(() => {
    getprofile();
  },)
  const getprofile = (item) => {
    const data = {
      _id: musicId ? musicId : item
    }
    console.log("gggggdsfasf4", item);
    axios.post("http://157.66.191.24:3003/api/get_single_music", data).then((res) => {
      //console.log(res);
      setvideoData(res.data.data)
    }).catch((error) => {
      console.log("error", error)
    })

  }

  useEffect(() => {
    getmusic();
  }, [0])
  const getmusic = () => {
    axios.get("http://157.66.191.24:3003/api/get_music").then((res) => {
      setMusicData(res.data.data)
    }).catch((error) => {
      console.log("error", error)
    })
  }

  return (
    <div>
      <div className="single-channel-page" id="content-wrapper" >
        <div className="container-fluid pb-0">
          <div className="video-block section-padding">
            <div className="row">
              <div className="col-md-8">

                <div className="single-video-left" >
                  <img className="img-fluid" style={{ height: "300px", width: "100%", borderRadius: "5px" }} src={`http://157.66.191.24:3003/uploads/` + musicData?.music[1]?.filename} alt="Music Thumbnail" />

                  <div className="single-video"   >

                    <ReactAudioPlayer
                      style={{ width: "100%", }}
                      src={`http://157.66.191.24:3003/uploads/` + musicData?.music[0]?.filename}

                      autoPlay

                      controls
                    />
                    {/* <ReactPlayer width='100%'
                      height='100%' controls='true' url={`http://157.66.191.24:3003/uploads/` + videoData?.music[0]?.filename} autoplay='true' playing='true' /> */}
                    {/* <iframe width="100%" height={315} src="https://www.youtube-nocookie.com/embed/8LWZSGNjuF0?rel=0&controls=0&showinfo=0" frameBorder={0} allow="autoplay; encrypted-media" allowFullScreen /> */}
                  </div>
                  <div className="single-video-title box mb-3">

                    <h2><a href="#">{musicData?.music_title}</a></h2>
                    <p className="mb-0"><i className="fas fa-eye" />{musicData?.audio_views}</p>
                  </div>
                  <div className="single-video-author box mb-3">
                    <div className="float-right"><button className="btn btn-danger" type="button">Subscribe <strong>{musicData?.data[0]?.subscribe_count}</strong></button> <button className="btn btn btn-outline-danger" type="button"><i className="fas fa-bell" /></button></div>
                    <img className="img-fluid" src="img/s4.png" alt />
                    <p><a href="#"><strong>{musicData?.data[0]?.channel_name}</strong></a> <span title data-placement="top" data-toggle="tooltip" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></span></p>
                    <small>Published on Aug 10, 2018</small>
                  </div>
                  <div className="single-video-info-content box mb-3">
                    <h6>Cast:</h6>
                    <p>Nathan Drake , Victor Sullivan , Sam Drake , Elena Fisher</p>
                    <h6>Category :</h6>
                    <p>Gaming , PS4 Exclusive , Gameplay , 1080p</p>
                    <h6>About :</h6>
                    <p>It is a long established fact </p>
                    <p className="tags mb-0">
                      <span><a href="#">Uncharted 4</a></span>
                      <span><a href="#">Playstation 4</a></span>
                      <span><a href="#">Gameplay</a></span>
                      <span><a href="#">1080P</a></span>
                      <span><a href="#">ps4Share</a></span>
                      <span><a href="#">+ 6</a></span>
                    </p>
                  </div>
                </div>

              </div>
              <div className="col-md-4">
                <div className="single-video-right">
                  <div className="row">
                    <div className="col-md-12">

                      <div className="main-title">
                        <div className="btn-group float-right right-action">
                          <a href="#" className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            Sort by <i className="fa fa-caret-down" aria-hidden="true" />
                          </a>
                          <div className="dropdown-menu dropdown-menu-right">
                            <a className="dropdown-item" href="#"><i className="fas fa-fw fa-star" /> &nbsp; Top Rated</a>
                            <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a>
                            <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                          </div>
                        </div>
                        <h6>Up Next</h6>
                      </div>
                    </div>
                    <div className="col-md-12" style={{ overflow: 'scroll', height: '600px' }}>

                      {MusicData?.map((data) => {
                        return (
                          <div className="video-card video-card-list" >
                            <div className="video-card-image">
                              <a onClick={(() => {
                                localStorage.setItem("musicid", data?._id)
                                getprofile(data?._id)
                              })} className="play-icon" href="#"><i className="fas fa-play-circle" /></a>
                              <Link onClick={(() => {
                                localStorage.setItem("musicid", data?._id)
                                getprofile(data?._id)
                              })}

                                className="play-icon" href="#"><i className="fas fa-play-circle" /></Link>
                              <Link onClick={(() => {
                                localStorage.setItem("musicid", data?._id)
                                getprofile(data?._id)
                              })}  >
                                {data?.music[1]?.filename ? <img style={{ height: "150px", width: "200px" }} className="img-fluid" src={`http://157.66.191.24:3003/uploads/` + data?.music[1]?.filename} alt /> :
                                  <img className="img-fluid" src="img/v2.png" alt />}
                              </Link>
                              {/* <div className="time">3:50</div> */}
                            </div>
                            <div className="video-card-body">
                              <div className="btn-group float-right right-action">
                                <a href="#" className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                  <i className="fa fa-ellipsis-v" aria-hidden="true" />
                                </a>
                                <div className="dropdown-menu dropdown-menu-right">
                                  <a className="dropdown-item" href="#"><i className="fas fa-fw fa-star" /> &nbsp; Top Rated</a>
                                  <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a>
                                  <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                                </div>
                              </div>
                              <div className="video-title">
                                <a href="#">{data?.music_title}</a>
                              </div>
                              <div className="video-page text-success">
                                {data?.data[0]?.channel_name}   <a title data-placement="top" data-toggle="tooltip" href="#" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></a>
                              </div>
                              <div className="video-view">
                                {data?.audio_views} views &nbsp;<i className="fas fa-calendar-alt" />{data?.current_date}
                              </div>
                            </div>
                          </div>
                        )
                      })}


                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>

    </div >
  )
}

export default Musicplay