import React, { useEffect, useState } from 'react'
import axios from 'axios';


function Createchannel() {



  const [Channelname, setChannelname] = useState();
  const [Coverimg, setCoverimg] = useState();
  const [Profileimg, setProfileimg] = useState();
  const [Description, setDescripation] = useState();

  let userid = localStorage.getItem("currentuserid")

  console.log(Channelname);
  console.log(Coverimg);
  console.log(Profileimg);
  console.log(Description);

  const createHandler = (e) => {
    e.preventDefault();



    const formData = new FormData();
    formData.append("user_id", userid)
    formData.append("channel_name", Channelname)
    formData.append("cover_image", Coverimg)
    formData.append("profile_image", Profileimg)
    formData.append("handle", Description)
    axios.post("http://157.66.191.24:3003/api/create_channel", formData)
      .then((res) => {

        // console.log("sdsd", res);

      })
      .catch((error) => {
        // console.log("sasasaa", error);
      });

  };
  const [imgData, setimgData] = useState();
  useEffect(() => {
    getprofile();
  }, [0])
  const getprofile = () => {
    const data = {
      user_id: userid
    }
    axios.post("http://157.66.191.24:3003/api/get_my_channel", data).then((res) => {
      console.log("sadsad", res);
      setimgData(res.data.data)
    }).catch((error) => {

    })

  }


  return (
    <div>
      <div className="single-channel-page" id="content-wrapper">
        {imgData?.map((data) => {
          return (
            <div className="single-channel-image">
              {data.image ? <img className="img-fluid cover " style={{ height: '400px' }} alt="" src={`http://157.66.191.24:3003/uploads/` + data?.image[1]?.filename} /> :
                <img class="img-fluid cover" alt="" src="img/channel-banner.png" />}
              <div className="channel-profile">



                {data?.image ? (<img alt="Avatar" src={`http://157.66.191.24:3003/uploads/` + data?.image[0]?.filename} className="channel-profile-img" />) : (<img class="channel-profile-img"
                  src="img/s2.png" />)}


              </div>
            </div>
          )
        })}
        {imgData?.map((data) => {
          return (

            <div className="single-video-author box mb-3">
              <div className="float-right "><button className="btn btn-danger" type="button">Subscribe <strong>{data?.subscribe_count}</strong></button> <button className="btn btn btn-outline-danger" type="button"><i className="fas fa-bell" /></button></div>
              <span style={{ display: "flex" }}>  <h5> {data?.channel_name}</h5>
                <i className="fas fa-check-circle text-success mt-2 ml-1" /></span>

            </div>
          )
        })}
      </div>



      <div className="single-channel-page" id="content-wrapper">
        <div className="container-fluid upload-details">
          <div className="row">
            <div className="col-lg-12">
              <div className="main-title">
                <h6>Create Channel</h6>
              </div>
            </div>
          </div>
          <hr />
          <form onSubmit={createHandler}>
            <div className="row">
              <div className="col-lg-12">
                <div className="osahan-form">
                  <div className="row">
                    <div className="col-lg-6">
                      <div className="form-group">
                        <label htmlFor="channelName" >Channel Name</label>
                        <input required type="text" placeholder="Enter Channel Name" id="channelName" className="form-control" onChange={(e) => { setChannelname(e.target.value) }} />
                      </div>
                      <div className="form-group">
                        <label htmlFor="channelName">cover image</label>
                        <input required type="file" placeholder="Enter Channel Name" id="channelName" className="form-control" onChange={(e) => { setCoverimg(e.target.files[0]) }} />
                      </div>
                      <div className="form-group">
                        <label htmlFor="channelName"> profile image </label>
                        <input required type="file" placeholder="Enter Channel Name" id="channelName" className="form-control" onChange={(e) => { setProfileimg(e.target.files[0]) }} />
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="form-group">
                        <label htmlFor="channelDescription">Channel Description</label>
                        <textarea required rows="3" id="channelDescription" name="channelDescription" type="text" className="form-control" placeholder="Enter Channel Description" onChange={(e) => { setDescripation(e.target.value) }}></textarea>
                      </div>
                    </div>
                  </div>


                </div>
                <div className="osahan-area text-center mt-3">
                  <button className="btn btn-outline-primary" type='submit'>Create Channel</button>
                </div>
                <hr />
                <div className="terms text-center">
                  <p className="mb-0">
                    By creating a channel, you agree to our <a href="#">Terms of Service</a> and <a href="#">Community Guidelines</a>.
                  </p>
                  <p className="hidden-xs mb-0">
                    Ensure your content complies with our policies.
                  </p>
                </div>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>

  )
}

export default Createchannel
