import React, { useEffect, useState } from 'react'
import axios from 'axios';
import { Link } from 'react-router-dom';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';



function Singlechannel() {
  const [channelData, setchannelData] = useState();
  const channelid = localStorage.getItem("channelID");
  const singlechannerid = localStorage.getItem("singleuserID")

  useEffect(() => {
    getchannel();
  }, []);

  const getchannel = () => {
    const data = {
      _id: channelid
    };
    axios.post("http://157.66.191.24:3003/api/get_single_channel", data)
      .then((res) => {
        //console.log("resss", res)
        setchannelData(res.data.data);
      })
      .catch((error) => {
        // console.error(error); 
      });
  };

  const [getvideo, setvideodata] = useState()

  useEffect(() => {
    getchannelvideo();
  }, []);
  const getchannelvideo = () => {
    const data = {
      user_id: singlechannerid,
      channel_id: channelid

    };
    axios.post("http://157.66.191.24:3003/api/get_my_channel_video", data)
      .then((res) => {

        setvideodata(res.data.data)

      })
      .catch((error) => {
        // console.error(error); 
      });
  };

  const [getmusic, setmusic] = useState()

  useEffect(() => {
    getchannelmusic();
  }, []);
  const getchannelmusic = () => {
    const data = {
      user_id: singlechannerid,
      channel_id: channelid

    };
    axios.post("http://157.66.191.24:3003/api/get_my_channel_audio", data)
      .then((res) => {

        setmusic(res.data.data)

      })
      .catch((error) => {

      });
  };


  return (

    <div className="single-channel-page" id="content-wrapper">
      <Tabs>

        <div className="single-channel-image">
          {channelData?.image[0] ? <img className="img-fluid " style={{ height: '400px', objectFit: 'cover' }} alt="" src={`http://157.66.191.24:3003/uploads/` + channelData?.image[1]?.filename} /> :
            <img class="img-fluid" alt="" src="img/channel-banner.png" />}
          <div className="channel-profile">



            {channelData?.image[0] ? (<img alt="Avatar" src={`http://157.66.191.24:3003/uploads/` + channelData?.image[0]?.filename} className="channel-profile-img" />) : (<img class="channel-profile-img"
              src="img/s2.png" />)}



          </div>
        </div>





        <div className="single-channel-nav" >
          <nav className="navbar navbar-expand-lg navbar-light">
            <a className="channel-brand" href="#">{channelData?.channel_name} <span title="" data-placement="top" data-toggle="tooltip" data-original-title="Verified"><i class="fas fa-check-circle text-success"></i></span></a>


            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>

            <div class="collapse navbar-collapse " id="navbarSupportedContent">
              <TabList className="navbar-nav mr-auto">
                <Tab className="nav-item active">
                  <Link className="nav-link" to=''>Videos <span class="sr-only"></span></Link>
                </Tab>
                <Tab className="nav-item">
                  <Link class="nav-link" href="#">Music</Link>
                </Tab>
                <Tab className="nav-item">
                  <Link class="nav-link" to='/home'>Home</Link>
                </Tab>
                <Tab className="nav-item">
                  <a class="nav-link" href="#">Discussion</a>
                </Tab>
                <Tab className="nav-item">
                  <a class="nav-link" href="#">About</a>
                </Tab>
                <Tab className="nav-item dropdown">
                  <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                    Donate
                  </a>

                </Tab>
              </TabList>
              <form className="form-inline my-2 my-lg-0 ">
                <input class="form-control form-control-sm mr-sm-1" type="search" placeholder="Search" aria-label="Search" /><button class="btn btn-outline-success btn-sm my-2 my-sm-0" type="submit"><i class="fas fa-search"></i></button> &nbsp;&nbsp;&nbsp; <button class="btn btn-outline-danger btn-sm" type="button">Subscribe <strong>1.4M</strong></button>
              </form>
            </div>
          </nav>
        </div>




        <TabPanel>

          <div className='row' >

            {getvideo?.map((data) => {

              return (


                <div className="col-md-4 col-sm-6 mb-3  mt-5">
                  <div className="video-card" id="content-wrapper">
                    <div className="video-card-image">
                      <Link to='/vpage' onClick={(() => { localStorage.setItem("videoid", data?._id) })} className="play-icon" href="#"><i className="fas fa-play-circle" /></Link>
                      <Link to='/vpage' onClick={(() => { localStorage.setItem("videoid", data?._id) })}>{
                        data?.video[1]?.filename ? <img className="img-fluid" style={{ height: '158px' }} src={`http://157.66.191.24:3003/uploads/` + data?.video[1]?.filename} alt /> :
                          <img className="img-fluid" src="img/v5.png" alt />
                      }</Link>

                    </div>

                    <div className="video-card-body">
                      <div className="col-md-12">
                        <div className="video-view">
                          <div className="btn-group float-right right-action">
                            <a href="#" className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                              <i style={{ color: "#dc3545" }} className="fas fa-fw fa-ellipsis-v" aria-hidden="true" />
                            </a>
                            <div className="dropdown-menu dropdown-menu-right">
                              {/* <Link className="dropdown-item" type='button'><i className="fas fa-fw fa-trash" /> &nbsp; Delete Video</Link> */}
                              <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a>
                              <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                            </div>


                          </div>
                        </div>
                      </div>
                      <div className="video-title">
                        <Link className='text-capitalize'>{data?.video_name}</Link>


                      </div>


                      <div className="video-page text-success text-capitalize">
                        {data?.channel_name}<a title data-placement="top" data-toggle="tooltip" href="#" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></a>
                      </div>

                      <div className="video-view">
                        {data?.video_views} views &nbsp;<i className="fas fa-calendar-alt" /> {data?.current_date}
                      </div>

                    </div>
                  </div>
                </div>

              )

            })

            }

          </div>
        </TabPanel>
        <TabPanel>
          <div className='row' >
            {getmusic?.map((data) => {
              return (

                <div className="col-xl-3 col-sm-6 mb-3 mt-5">
                  <div className="video-card history-video">
                    <div className="video-card-image">
                      <a className="video-close" href="#"></a>
                      <Link to='/play' onClick={(() => { localStorage.setItem("musicid", data?._id) })} className="play-icon" href="#"></Link>
                      <Link onClick={(() => { localStorage.setItem("musicid", data?._id) })}  >
                        {data?.music[1]?.filename ? <img style={{ height: "150px" }} className="img-fluid" src={`http://157.66.191.24:3003/uploads/` + data?.music[1]?.filename} alt /> :
                          <img className="img-fluid" src="img/v2.png" alt />}
                      </Link>

                    </div>
                    <div className="progress">

                      <div className="progress-bar" role="progressbar" style={{ width: '50%' }} aria-valuenow={50} aria-valuemin={0} aria-valuemax={100}>1:40</div>
                    </div>
                    <div className="video-card-body">
                      <div className="col-md-12">
                        <div className="video-view">
                          <div className="btn-group float-right right-action" >
                            <a className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                              <i style={{ color: "#dc3545" }} className="fas fa-fw fa-ellipsis-v" aria-hidden="true" />

                            </a>
                            <div className="dropdown-menu dropdown-menu-right">
                              {/* <Link className="dropdown-item" type='button'><i className="fas fa-fw fa-trash" /> &nbsp; Delete Video</Link> */}

                              <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a>
                              <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                            </div>
                          </div>

                        </div>
                      </div>
                      <div className="video-title">
                        <Link onClick={(() => { localStorage.setItem("musicid", data?._id) })} className='text-capitalize'>{data?.music_title}</Link>
                      </div>

                      <div className="video-page text-success text-capitalize">
                        {data?.data[0]?.channel_name}  <Link onClick={(() => { localStorage.setItem("musicid", data?._id) })} title data-placement="top" data-toggle="tooltip" href="#" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></Link>
                      </div>

                      <div className="video-view">

                        {data?.audio_views} views &nbsp;<i className="fas fa-calendar-alt" />{data?.current_date}
                      </div>

                    </div>
                  </div>
                </div>

              )
            })}

          </div>
        </TabPanel>


      </Tabs>
    </div>
  )
}

export default Singlechannel
