import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Toaster, toast } from 'react-hot-toast';
import { Navigate, useNavigate } from 'react-router-dom';



function Uploadvideo() {

  const Navigate = useNavigate();
  const [videoname, setvideoname] = useState();
  const [description, setdescription] = useState();
  const [categorytype, setcategorytype] = useState();
  const [uservideo, setuservideo] = useState();
  const [videothum, setvideothum] = useState();


  let channelid = localStorage.getItem("curntchannelid")
  let userid = localStorage.getItem("currentuserid");
  let channelName = localStorage.getItem("curntchannelname")

  const Uploadvideo = (e) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("channel_id", channelid)
    formData.append("user_id", userid)
    formData.append("channel_name", channelName)
    formData.append("video_name", videoname)
    formData.append("desc", description)
    formData.append("category_type", categorytype)
    formData.append("user_video", uservideo)
    formData.append("video_thumbnail", videothum)
    formData.append("time_zone", "44411")


    axios.post("http://157.66.191.24:3003/api/upload_video", formData).then((res) => {

      toast.success(res.data.msg);

      setTimeout(() => {
        Navigate("/home")
      }, 3000)
    }).catch((error) => {
      toast.error("Failed to video uplode!");


    })
  }




  return (
    <div>
      <Toaster />
      <div className="single-channel-page" id="content-wrapper">
        <form onSubmit={Uploadvideo}>
          <div className="container-fluid upload-details">
            <div className="row">
              <div className="col-lg-12">
                <div className="main-title">
                  <h6>Upload Details</h6>
                </div>
              </div>
              <div className="col-lg-2">
                <div className="imgplace"></div>
              </div>

            </div>
            <div className="row mt-5">
              <div className="col-lg-12">
                <div className="osahan-form">

                  <div className="row">

                    <div className="col-lg-6">
                      <div className="form-group">
                        <label htmlFor="e1">Video Name</label>
                        <input required type="text" placeholder="Enter Video Name" id="e1" className="form-control" onChange={((e) => setvideoname(e.target.value))} />
                      </div>
                    </div>

                    <div className="col-lg-6">
                      <div className="form-group">
                        <label htmlFor="e1">categorytype</label>
                        <select required onChange={((e) => setcategorytype(e.target.value))} className="form-control">
                          <option value='' selected>Select Video Category</option>
                          <option value="BOLLYWOOD">BOLLYWOOD </option>
                          <option value="HOLLYWOOD">HOLLYWOOD </option>
                        </select>
                        {/* <input type="text" placeholder="" id="e1" className="form-control" onChange={((e) => setcategorytype(e.target.value))} /> */}
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="form-group">
                        <label htmlFor="e1">User Video </label>
                        <input required type="file" accept="video/*" placeholder="No File Chosen" id="e1" className="form-control"
                          onChange={((e) => setuservideo(e.target.files[0]))} />
                      </div>
                    </div>
                    <div className="col-lg-6">
                      <div className="form-group">
                        <label htmlFor="e1">Video Thumbnail</label>
                        <input required type="file" accept=".jpg, .png image/*" placeholder="No File Chosen" id="e1" className="form-control" onChange={((e) => setvideothum(e.target.files[0]))} />
                      </div>
                    </div>
                    <div className="col-sm-12">
                      <div className="form-group">
                        <label className="control-label">Description <span className="required">*</span></label>
                        <textarea required type="text" placeholder="Video Description" className="form-control border-form-control" defaultValue={""} onChange={((e) => setdescription(e.target.value))} />
                      </div>

                    </div>
                  </div>
                </div>

              </div>
            </div>
            <hr />
            <div className="osahan-area text-center mt-3">
              <button className="btn btn-outline-primary" type='submit'>Upload Video</button>
            </div>
          </div>
        </form>

      </div>
    </div>
  )
}

export default Uploadvideo