import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import toast, { Toaster } from 'react-hot-toast';




function Downlodes() {
  const [downlodevideo, setdownlodevideo] = useState()

  let userid = localStorage.getItem("currentuserid")


  useEffect(() => {
    getdownlode();
  }, [0])
  const getdownlode = () => {
    const data = {
      user_id: userid,

    }

    axios.post("http://157.66.191.24:3003/api/get_download_video", data).then((res) => {
      console.log("sadsadsad", res);
      setdownlodevideo(res.data.data)
    }).catch((error) => {

    })

  }
  const [deletevideodata, setdeletevideodata] = useState();
  console.log("idddd", deletevideodata)
  const deletevideo = () => {
    const data = {
      user_id: userid,
      video_id: deletevideodata,

    }
    console.log("xxdataa", data)
    axios.post("http://157.66.191.24:3003/api/delete_download_video", data).then((res) => {
      getdownlode();
      // console.log("res", res)
      toast.success(res.data.msg)
    }).catch((error) => {
      console.log("error", error)
    })

  }




  return (
    <div>
      <Toaster />
      <div className="single-channel-page" id="content-wrapper">
        <div className="container-fluid">
          <div className="video-block section-padding">
            <div className="row">
              <div className="col-md-12">
                <div className="main-title">

                  <h6>Downlodes video</h6>
                </div>
              </div>

              {downlodevideo?.map((data) => {
                return (
                  <div className="col-xl-3 col-sm-6 mb-3">
                    <div className="video-card history-video">
                      <div className="video-card-image">

                        <Link to='/vpage' onClick={(() => { localStorage.setItem("videoid", data?.video_id) })} className="play-icon" href="#"><i className="fas fa-play-circle" /></Link>
                        <Link to='/vpage' onClick={(() => { localStorage.setItem("videoid", data?.video_id) })} >{
                          data.video_data[0]?.video[1]?.filename ? <img className="img-fluid" style={{ height: '158px' }} src={`http://157.66.191.24:3003/uploads/` + data.video_data[0]?.video[1]?.filename} alt /> :
                            <img className="img-fluid" src="img/v1.png" alt />}
                        </Link>
                        <div className="time">3:50</div>
                      </div>

                      <div className="video-card-body ">
                        <div className="video-title text-capitalize">
                          <a href="#">{data.video_data[0]?.video_name}</a>
                          <div className="btn-group float-right right-action">
                            <a onClick={(() => setdeletevideodata(data?.video_id))} href="#" className="right-action-link text-gray" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                              <i style={{ color: "#dc3545" }} className="fas fa-fw fa-ellipsis-v" aria-hidden="true" />
                            </a>
                            <div className="dropdown-menu dropdown-menu-right">
                              <Link onClick={deletevideo} className="dropdown-item" type='button'><i className="fas fa-fw fa-trash" /> &nbsp; Delete Video</Link>
                              {/* <a className="dropdown-item" href="#"><i className="fas fa-fw fa-signal" /> &nbsp; Viewed</a> */}
                              <a className="dropdown-item" href="#"><i className="fas fa-fw fa-times-circle" /> &nbsp; Close</a>
                            </div>


                          </div>
                        </div>
                        <div className="video-page text-success text-capitalize">
                          {data.video_data[0]?.channel_name}<a title data-placement="top" data-toggle="tooltip" href="#" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></a>
                        </div>
                        <div className="video-view">
                          {data.video_data[0]?.video_views} views &nbsp;<i className="fas fa-calendar-alt" />{data.video_data[0]?.current_date}
                        </div>
                      </div>
                    </div>
                  </div>)

              })

              }

            </div>
            <nav aria-label="Page navigation example">
              <ul className="pagination justify-content-center pagination-sm mb-0">
                <li className="page-item disabled">
                  <a className="page-link" href="#" tabIndex={-1}>Previous</a>
                </li>
                <li className="page-item active"><a className="page-link" href="#">1</a></li>
                <li className="page-item"><a className="page-link" href="#">2</a></li>
                <li className="page-item"><a className="page-link" href="#">3</a></li>
                <li className="page-item">
                  <a className="page-link" href="#">Next</a>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Downlodes
