
import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Routes, useLocation } from 'react-router-dom';
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import Sidebar from './components/Sidebar';
import Home from './components/Home';
import Channels from './components/Channels';
import Singlechannel from './components/Singlechannel';
import Videopage from './components/Videopage';
import Uploadvideo from './components/Uploadvideo';
import Login from './components/Login';
import Register from './components/Register';
import Forgotpassword from './components/Forgotpassword';
import History from './components/History';
import Categories from './components/Categories';
import Setting from './components/Setting';
import Subscriptions from './components/Subscriptions';
import Account from './components/Account';
import Logoutm from './components/Logoutm';
import Music from './components/Music';
import Likedvideo from './components/Likedvideo';
import Uploadmusic from './components/Uploadmusic';
import Downlodes from './components/Downlodes';
import Watchlater from './components/Watchlater';
import Createchannel from './components/Createchannel';
import CreateProfile from './components/CreateProfile';
import Protect from './components/Protect';
import Musicplay from './components/Musicplay';
import Help from './components/Help';

function App() {
  const location = useLocation();

  return (

    <>
      {location.pathname !== '/login' && location.pathname !== '/register' && location.pathname !== '/for' && <Navbar />}
      {location.pathname !== '/login' && location.pathname !== '/register' && location.pathname !== '/for' && <Sidebar />}


      {/* <Protect /> */}


      <Routes>
        <Route path='/home' element={<Home />}></Route>
        {/* <Route path="/home" element={<Protect ComponentName={Home} />}></Route> */}
        <Route path='/channels' element={<Channels />}></Route>
        <Route path='/spage' element={<Singlechannel />}></Route>
        <Route path='/vpage' element={<Videopage />}></Route>
        <Route path='/mpage' element={<Music />}></Route>
        <Route path='/login' element={<Login />}></Route>
        <Route path='/register' element={<Register />}></Route>
        <Route path='/for' element={<Forgotpassword />}></Route>
        <Route path='/play' element={<Musicplay />}></Route>
        <Route path='/log' element={< Logoutm />}></Route>
        <Route path='/cate' element={<Protect ComponentName={Categories} />}></Route>
        <Route path='/set' element={<Protect ComponentName={Setting} />}></Route>
        <Route path='/like' element={<Protect ComponentName={Likedvideo} />}></Route>
        <Route path='/watch' element={<Protect ComponentName={Watchlater} />}></Route>
        <Route path='/his' element={<Protect ComponentName={History} />}></Route>
        <Route path='/sub' element={<Protect ComponentName={Subscriptions} />}></Route>
        <Route path='/create' element={<Protect ComponentName={Createchannel} />}></Route>
        <Route path='/profile' element={<Protect ComponentName={CreateProfile} />}></Route>
        <Route path='/uploadm' element={<Protect ComponentName={Uploadmusic} />}></Route>
        <Route path='/upload' element={<Protect ComponentName={Uploadvideo} />}></Route>
        <Route path='/account' element={<Protect ComponentName={Account} />}></Route>
        <Route path='/down' element={<Protect ComponentName={Downlodes} />}></Route>
        <Route path='/help' element={<Help />}></Route>

      </Routes>
      {location.pathname !== '/login' && location.pathname !== '/register' && location.pathname !== '/for' && <Footer />}


    </>




  );
}

export default App;
