import React, { useState } from 'react'
import axios from 'axios';
import { useEffect } from 'react';
import { Link } from 'react-router-dom';




function History() {
  const [history, sethistory] = useState()
  console.log("sss", history)
  let userid = localStorage.getItem("currentuserid")

  useEffect(() => {
    getHistory();
  }, [])
  const getHistory = () => {

    const data = {
      user_id: userid
    }
    axios.post("http://157.66.191.24:3003/api/get_history", data).then((res) => {

      // console.log("lllll", res);
      sethistory(res.data.data);

    }).catch((error) => {
      console.log("mmmmm", error);
    })

  }
  return (
    <div>
      <div className="single-channel-page" id="content-wrapper">
        <div className="container-fluid">
          <div className="video-block section-padding">
            <div className="row">
              <div className="col-md-12">
                <div className="main-title">

                  <h6>Watch History</h6>
                </div>
              </div>


              {history?.map((data) => {
                return (<div className="col-xl-3 col-sm-6 mb-3">
                  <div className="video-card">
                    <div className="video-card-image">
                      <Link to='/vpage' onClick={(() => {
                        localStorage.setItem("videoid", data?.data[0]?._id)
                        localStorage.setItem("channelID", data?.data[0]?.channel_id)
                      })} className="play-icon" href="#"><i className="fas fa-play-circle" /></Link>
                      <Link to='/vpage' onClick={(() => {
                        localStorage.setItem("videoid", data?.data[0]?._id)
                        localStorage.setItem("channelID", data?.data[0]?.channel_id)
                      })}>{
                          data?.data[0]?.video[1]?.filename ? <img className="img-fluid" style={{ height: '158px' }} src={`http://157.66.191.24:3003/uploads/` + data?.data[0]?.video[1]?.filename} alt /> :
                            <img className="img-fluid" src="img/v5.png" alt />
                        }</Link>

                    </div>

                    <div className="video-card-body">
                      <div className="col-md-12">

                      </div>
                      <div className="video-title">
                        <Link >{data?.data[0]?.video_name}</Link>


                      </div>


                      <div className="video-page text-success">
                        {data?.data[0]?.channel_name}<a title data-placement="top" data-toggle="tooltip" href="#" data-original-title="Verified"><i className="fas fa-check-circle text-success" /></a>
                      </div>

                      <div className="video-view">
                        {data?.video_views} views &nbsp;<i className="fas fa-calendar-alt" /> {data?.current_date}
                      </div>

                    </div>
                  </div>
                </div>)

              })

              }


            </div>
            <nav aria-label="Page navigation example">
              <ul className="pagination justify-content-center pagination-sm mb-0">
                <li className="page-item disabled">
                  <a className="page-link" href="#" tabIndex={-1}>Previous</a>
                </li>
                <li className="page-item active"><a className="page-link" href="#">1</a></li>
                <li className="page-item"><a className="page-link" href="#">2</a></li>
                <li className="page-item"><a className="page-link" href="#">3</a></li>
                <li className="page-item">
                  <a className="page-link" href="#">Next</a>
                </li>
              </ul>
            </nav>
          </div>
        </div>
      </div>

    </div>
  )
}

export default History
